import React from 'react';
import ReactDOM from 'react-dom';
import {init as firebaseInit, database} from './firebase'
import './index.css';

function Square(props) {
    var name = props.highlight ? 'square-highlighted' : 'square';
    return (
        <button className={name} onClick={props.onClick}>
            {props.value}
        </button>
    )
}

class Board extends React.Component {
    renderSquare(i) {
        var isHighlighted = false;
        if(this.props.winner) {
            for(var square of this.props.winner) {
                if(square === i) {
                    isHighlighted = true;
                }
            }
        }
        return (
            <Square
                value={this.props.squares[i]}
                onClick={() => this.props.onClick(i)}
                highlight={isHighlighted}
            />
        );
    }

    render() {
        var board = [];
        for (var i = 0; i < 3; i++){
            var row = [];
            for (var j = 0; j < 3; j++) {
                row.push(this.renderSquare(3*i+j))
            }
            board.push(<div className="board-row">{row}</div>);
        }

        return (
            <div>{board}</div>
        );
    }
}

class Game extends React.Component {
    constructor() {
        super();
        firebaseInit()
        this.state = {
            history: [{
                squares: Array(9).fill(''),
            }],
            stepNumber: 0,
            xIsNext: true,
            moveCoord: null,
            activeMove: null,
        };
        // database.child('history').set(this.state.history)
        // database.child('stepNumber').set(this.state.stepNumber)
        // database.child('xIsNext').set(this.state.xIsNext)
        // database.child('moveCoord').set(this.state.moveCoord)
    }

    handleClick(i) {
        const history = this.state.history.slice(0, this.state.stepNumber + 1);
        const current = history[history.length - 1];
        const squares = current.squares.slice();
        if (calculateWinner(squares) || squares[i]) {
            return;
        }
        squares[i] = this.state.xIsNext ? 'H' : 'L';

        // this.setState({
        //     history: history.concat([{
        //         squares: squares,
        //     }]),
        //     stepNumber: history.length,
        //     xIsNext: !this.state.xIsNext,
        //     moveCoord: [Math.floor((i)/3)+1,Math.floor((i)%3)+1],
        //     activeMove: null,
        // });

        database.child('history').set(history.concat([{
            squares: squares,
        }]))
        database.child('stepNumber').set(history.length)
        database.child('xIsNext').set(!this.state.xIsNext)
        database.child('moveCoord').set([Math.floor((i)/3)+1,Math.floor((i)%3)+1])
    }

    jumpTo(step) {
        this.setState({
            stepNumber: step,
            xIsNext: (step % 2) === 0,
            activeMove: step,
        });
    }

    componentDidMount() {
        // firebaseInit()
        database.on('value', snapshot => {
            this.setState({
                history: snapshot.child('history').val(),
                stepNumber: snapshot.child('stepNumber').val(),
                xIsNext: snapshot.child('xIsNext').val(),
                moveCoord: snapshot.child('moveCoord').val(),
            });
        });
    }

    render() {
        const history = this.state.history;
        const current = history[this.state.stepNumber];
        const winner = calculateWinner(current.squares);
        const lastMove = this.state.moveCoord ?
            'Last move: ' + this.state.moveCoord[0]+','+this.state.moveCoord[1] : 'Last move:'

        const moves = history.map((step, move) => {
            const desc = move ?
                'Move #' + move :
                'Game start';
            var moveSelected = this.state.activeMove === move ? 'move-selected' : ''
            return (
                <li key={move}>
                    <a className={moveSelected} href="#" onClick={() => this.jumpTo(move)}>{desc}</a>
                </li>
            );
        });

        let status;
        if (winner) {
            status = 'Winner: ' + current.squares[winner[0]];
        } else {
            status = 'Next player: ' + (this.state.xIsNext ? 'Hedit' : 'Louis');
        }

        return (
            <div className="game">
                <div className="game-board">
                    <Board
                        squares={current.squares}
                        onClick={(i) => this.handleClick(i)}
                        winner={winner}
                    />
                </div>
                <div className="game-info">
                    <div>{status}</div>
                    <div>{lastMove}</div>
                    <ol>{moves}</ol>
                </div>
            </div>
        );
    }
}

function calculateWinner(squares) {
    const lines = [
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],
        [0, 4, 8],
        [2, 4, 6],
    ];
    for (let i = 0; i < lines.length; i++) {
        const [a, b, c] = lines[i];
        if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
            return lines[i];
        }
    }
    return false;
}

// ========================================

ReactDOM.render(
    <Game />,
    document.getElementById('root')
);




